export default {
  data() {
    return {
      mobile: '',
      password: '',
      showDel: false,
      ishide: false,
      loginType: ''
    }
  },
  onLoad() {
    // uni.getStorage({
    //   key: 'userinfo',
    //   success: function (res) {
    //     if(res.data.token) {
    //       console.log('登录页本地获取的token是', res.data.token)
    //       if(res.data.mobile) {
    //         console.log('登录页本地获取的手机号是', res.data.mobile)
    //         uni.reLaunch({
    //           url: '../index/index'
    //         })
    //         return
    //       }
    //       return
    //     }
    //   }
    // })
  },
  methods: {
    onInput(e) {
      let value = e.detail.value
      if (value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    clickAction(type) {
      uni.navigateTo({
        url: 'register?type=' + type,
        "animationType": "zoom-fade-out"
      })
    },
    goLogin() {
      let value = /^1[3456789]\d{9}$/.test(this.mobile)
      if(value) {
        if(!this.password) {
          uni.showToast({
            title: '请输入密码',
            icon: 'none'
          })
          return
        }
        this.$http({
          url: this.$api.login,
          data: {
            mobile: this.mobile,
            password: this.password
          },
          cb: (err, res) => {
            if(!err && res.code === 1) {
              // console.log(res.data.userinfo.token)
              
              let userinfo = {
                mobile: res.data.userinfo.mobile,
                token: res.data.userinfo.token
              }
              this.$store.commit('login', userinfo)
              uni.setStorage({
                key: 'userinfo',
                data: userinfo,
                success: function () {
                  console.log('success')
                }
              })
              uni.switchTab({
                url: '../index/index'
              })  
            } else if(res.code === 0 && res.msg) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            } else {
              uni.showToast({
                title: '登录失败',
                icon: 'none'
              })
            }
          }
        })
      } else {
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
      }
    },
    
    // 点击第三方登录
    loginWay(type) {
      let that = this
      that.loginType = type === 'weixin' ? 'weixin': ( type === 'qq' ? 'qq' : 'sinaweibo' )
      uni.getProvider({
        service: 'oauth',
        success: function(res) {
          if (~res.provider.indexOf(type)) {
            uni.login({
              provider: type,
              success: function(loginRes) {
                // console.log('第三方登录获取到的信息', JSON.stringify(loginRes.authResult.openid))
                if(type === 'sinaweibo') {
                  that.openid = loginRes.authResult.uid
                } else {
                  that.openid = loginRes.authResult.openid
                }
                that.getInfo()
              }
            })
          }
        },
        fail: function(err){
          uni.showToast({
            title: '授权登录失败',
            icon: 'none'
          })
        }
      })
    },
    // 第三方登录获取绑定是否手机号等信息
    getInfo() {
      this.$http({
        url: this.$api.otherlogin,
        method: 'POST',
        data: {
          openid: this.openid,
          type: this.loginType
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            // console.log('获取成功', res.data.userinfo)
            
            // 绑定了手机
            if(res.data.userinfo.mobile) {
              let userinfo = {
                mobile: res.data.userinfo.mobile,
                token: res.data.userinfo.token
              }
              
              // 存储token信息
              this.$store.commit('login', userinfo)
              uni.setStorage({
                key: 'userinfo',
                data: userinfo,
                success: function () {
                  uni.switchTab({
                    url: '../index/index'
                  })
                }
              })
              return
            }
            
            // 没绑定手机
            this.$store.commit('setToken', res.data.userinfo.token)
            
            uni.showToast({
              title: '请绑定您的手机号码',
              icon: 'none'
            })
            uni.navigateTo({
              url: 'bindTel'
            })
            
          } else if (res.code === 0 && res.msg){
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } if(err) {
            uni.showToast({
              title: '获取用户绑定信息失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
